import React from "react";
import { Formik } from "formik";
import { Form, Input,DatePicker,Checkbox,Radio,Select, SubmitButton, ResetButton } from "formik-antd";
import { message, Space} from "antd";
import * as yup from "yup";
import "./TaskStyle.css";
import moment from "moment";
// import 'antd/dist/antd.css'

const { Option } = Select;

function Task() {
  const today = new Date();
  let options = [
    { label: "Feature", value: 1 },
    { label: "Front-End", value: 2 },
    { label: "Change-Request", value: 3 },
    { label: "Back-End", value: 4 },
  ];
  const initValues = {
    title: "",
    des: "",
    dueDate: "",
    priority:{value:"",label:""},
    type: "",
    label: [],
  };

  const validate = yup.object({
    title: yup.string().required("Required"),
    des: yup.string().required("Required"),
    dueDate: yup
      .date()
      .nullable()
      .max(today)
      .required("DueDate greater than today"),
    priority: yup
      .object()
      .shape({
        label: yup.string().required("Required"),
        value: yup.number().required,
      }),
    label: yup.array().min(1, "Select atleast one Label"),
    type: yup.string().required("Type Required"),
  });

  function handleSubmit(values) {
    let config = {
      method: "POST",
      body: JSON.stringify({
        title: values.title,
        description: values.des,
        dueDate: values.dueDate,
        priority: values.priority.value,
        label: [values.label],
        type: values.type,
      }),
      headers: { "content-type": "application/json" },
    };
    fetch("https://task-management-rest-app.herokuapp.com/api/tasks", config)
      .then((res) => {
        if (!res.ok) {
          message.error({ content: "Server Error", duration: 4 });
          return Promise.reject();
        } else return res.json();
      })
      .then((data) => console.log(data));
  }

  return (
    <Formik
      initialValues={initValues}
      onSubmit={handleSubmit}
      validationSchema={validate}
    >
      {(props) => (
        <div className="div">
          <Form>
            <Space direction="vertical">
              <Input
                id="title"
                type="text"
                name="title"
                value={props.values.title}
                onBlur={props.handleBlur}
                onChange={props.handleChange}
              />
              <Input.TextArea
                id="des"
                name="des"
                value={props.values.des}
                onBlur={props.handleBlur}
                onChange={props.handleChange}
              />
              <Space direction="horizontal">
                <DatePicker
                  className="dueDate"
                  onChange={()=> new Date(props.values.dueDate)}
                  onBlur={props.handleBlur}
                  name="dueDate"
                  value={props.values.dueDate}
                />
                <Radio.Group
                  className="type"
                  values={props.values.type}
                  onChange={props.handleChange}
                  name="type"
                >
                  <Radio value={1}>Task</Radio>
                  <Radio value={2}>Story</Radio>
                  <Radio value={3}>Bug</Radio>
                </Radio.Group>
              </Space>
              <Space direction="horizontal">
                <Select
                  className="priority"
                  name="priority"
                  placeholder="Priority"
                  onBlur={props.handleBlur}
                  value={props.values.priority.label}
                  onChange={props.handleChange}
                >
                  <Option value={1}>High</Option>
                  <Option value={2}>Medium</Option>
                  <Option value={3}>Low</Option>
                </Select>
                <Checkbox.Group
                  className="box"
                  options={options}
                  onChange={props.handleChange}
                  name="label"
                  checked={props.values.label}
                />
              </Space>
              <Space direction="horizontal">
                <SubmitButton className="btn">Submit</SubmitButton>
                <ResetButton className="btn">Reset</ResetButton>
              </Space>
            </Space>
          </Form>
        </div>
      )}
    </Formik>
  );
}

export default Task;
